<script>
	export default {
		data() {
			return {
				system_top: 44,
				scrollHeight: 0,
				goodsArray: [{
					num: 1
				}, {
					num: 1
				}, {
					num: 1
				}],
				payTypeList: [],
				selectIndex: 0
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			if (parseInt(res.safeArea.top) > this.system_top) {
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - this.system_top - 40 - 60 - 100
		},
		onLoad(option) {
			var that = this
			that.getPayList()
		},
		onUnload() {

		},
		methods: {

			doBack() {
				uni.navigateBack({

				})
			},

			xiadan() {
				uni.navigateTo({
					url: './next'
				})
			},
			payTypeClick(item, index) {
				this.selectIndex = index
			},
			getPayList() {
				var that = this
				//类型 1充值 2下单
				this.http.sendRequest('Orders.GetPayList', {
					'type': 2
				}).then(function(res) {
					that.payTypeList = res.info
				})
			}
		}
	}
</script>

